import React from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faTwitter, faYoutube } from "@fortawesome/free-brands-svg-icons"
import "./index.scss"

type Artist = {
  name: string
  twitter: string
  youtube: string
}

interface Props {
  artist: Artist
}

const Detail = (props: Props) => {
  return (
    <li className="detail__artist">
      <div>{props.artist.name}</div>
      <div className="detail__sns">
        <a href={props.artist.twitter} target="_blank">
          <FontAwesomeIcon className="icon--twitter" icon={faTwitter} />
        </a>
        <a href={props.artist.youtube} target="_blank">
          <FontAwesomeIcon className="icon--youtube" icon={faYoutube} />
        </a>
      </div>
    </li>
  )
}

export default Detail
