import React from "react"
import Image from "@vtv/components/atoms/image"
import Booth from "@vtv/components/molecules/booth"
import Detail from "@vtv/components/molecules/detail"
import Button from "@vtv/components/atoms/button"
import "./index.scss"

export type Circle = {
  id: number
  space: string
  circleName: string
  artists: Artist[]
  shops: Shop[]
}

export type Artist = {
  name: string
  twitter: string
  youtube: string
}

export type Shop = {
  name: string
  url: string
}

interface Props {
  circle: Circle
}

const Panel = ({ circle }: Props) => {
  return (
    <div className="panel" data-sal="slide-up" data-sal-easing="ease">
      <Booth space={circle.space} circleName={circle.circleName} />
      <div className="panel__detailBox">
        <div className="panel__circleCut">
          <img src={require(`@vtv/images/circles/${circle.id}.png`)} />
        </div>
        <div className="panel__artist">
          <div className="panel__artist--headTitle">Artist</div>
          <ul>
            {circle.artists.map(artist => {
              return (
                <Detail
                  artist={{
                    name: artist.name,
                    twitter: artist.twitter,
                    youtube: artist.youtube,
                  }}
                />
              )
            })}
          </ul>
        </div>
      </div>
      <div className="panel__imageBooth">
        {/* <Image src={require(`@vtv/images/circles/booth/${circle.id}.png`)} /> */}
      </div>
      <div className="panel__buyButton">
        <ul>
          {circle.shops.map(shop => {
            return (
              <li
                className={`${
                  shop.name.includes("Diverse") ? "longName" : "shortName"
                }`}
              >
                <Button href={shop.url}>{shop.name}</Button>
              </li>
            )
          })}
        </ul>
      </div>
    </div>
  )
}

export default Panel
