import React from "react"
import imageMap from "@vtv/images/map.png"
import imageSD1 from "@vtv/images/sd1.png"
import imageSD2 from "@vtv/images/sd2.png"
import Panel, { Circle, Artist } from "@vtv/components/molecules/panel"
import Head from "@vtv/components/atoms/head"
import "./index.scss"
import jsonData from "@vtv/data/circle.json"

const Artists = () => {
  const circleList: Array<Circle> = jsonData
  return (
    <div className="Artists">
      <Head>Map</Head>
      <div className="Artists__map">
        <img
          className="img--sd1"
          data-sal="slide-right"
          data-sal-easing="ease"
          data-sal-delay="300"
          src={imageSD1}
        />
        <img
          className="img--map"
          data-sal="slide-up"
          data-sal-easing="ease"
          data-sal-delay="100"
          src={imageMap}
        />
        <img
          className="img--sd2"
          data-sal="slide-left"
          data-sal-easing="ease"
          data-sal-delay="300"
          src={imageSD2}
        />
      </div>
      <h2>Artists</h2>
      <div className="Artists__circle">
        {circleList.map(circle => {
          return <Panel circle={circle}></Panel>
        })}
      </div>
    </div>
  )
}

export default Artists
