import React from "react"
import "./index.scss"

interface Props {
  space: string
  circleName: string
}

const Booth = (props: Props) => {
  return (
    <div className="booth">
      <span
        className={`booth__num ${
          props.space.includes("A") ? "spaceA" : "spaceB"
        }`}
      >
        {props.space}
      </span>
      <span className="booth__name">{props.circleName}</span>
    </div>
  )
}

export default Booth
